.spinner {
  border: 8px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  margin-left: -30px;
  animation: 1s linear infinite spin;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@media (width <= 600px) {
  .spinner {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
